body,html{
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.MuiListItemText-secondary{
  display: inline-block;
  padding: 10px;
  max-width: 70%;
  border-radius: 5px;
  background-color: rgb(201, 201, 201);
}
.nick-name{
  font-size: 12px;
  color: #333;
  position: relative;
  top: -6px;
  display: block;
  text-align: center;
  margin-right: 10px;
}

.my-message{
  flex-direction: row-reverse;
  text-align: right;
}
.my-message .MuiListItemText-root{
  text-align: right;
  margin-right: 20px;
}
.my-message .MuiListItemText-root .MuiTypography-root{
  background-color: #64dd17;
  text-align: left;
}

.main-container{
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.app-title{
  height: 65px;
}
/* 主要内容区 */
.contain-list{
  flex: 1 1;
  overflow: auto;
  min-height: 400px;
}
.footer{
  height: 80px;
}
.typo-title{
  flex-grow: 1;
}
